export default function Review(){
    const navigate = useNavigate();
    const [formValue, setFormValue] = useState({AreaRatings:3,CleanerRatings:3,OverallRatings:3,Comments:""});
    let { orderId } = useParams(); 
    useEffect(()=>{
        // console.log('window.mobileAndTabletCheck:'+window.mobileAndTabletCheck() );
    },[formValue]);
    return(
        <Styles>
            <h2>Let us hear your thoughts!</h2>
            <Form
                initialValues={formValue}
                // validate={this.validate}
                onSubmit={(e)=>{
                    console.log(e);
                    navigate('/landing');
                }}
            >
                {({ handleSubmit, submitting, values }) => (
                <form onSubmit={handleSubmit}>
                    <label><b>1: Unit/Area Rating</b></label><br/>
                    <label>Area is clean and well-maintained</label><br/>
                    <Rating
                    name='AreaRatings'
                    className='AreaRatings'
                    value={formValue.AreaRatings}
                    size="large"
                    sx={window.mobileAndTabletCheck()?{
                        width: "80%",
                        justifyContent: "space-between",
                        fontSize:"2.5rem"
                      }:{}}
                    onChange={(event, newValue) => {
                        const updatedValue = {}
                        updatedValue.AreaRatings = newValue
                        setFormValue({
                            ...formValue,
                            ...updatedValue
                        });
                    }}
                    /><br/><br/>
                    <label><b>2: Cleaner Rating</b></label><br/>
                    <label>Cleaner did the job well</label><br/>
                    <Rating
                    name='CleanerRatings'
                    className='CleanerRatings'
                    value={formValue.CleanerRatings}
                    size="large"
                    sx={window.mobileAndTabletCheck()?{
                        width: "80%",
                        justifyContent: "space-between",
                        fontSize:"2.5rem"
                      }:{}}
                    onChange={(event, newValue) => {
                        const updatedValue = {}
                        updatedValue.CleanerRatings = newValue
                        setFormValue({
                            ...formValue,
                            ...updatedValue
                        });
                    }}
                    /><br/><br/>
                    <label><b>3: Overall Rating</b></label><br/>
                    <Rating
                    name='OverallRatings'
                    className='OverallRatings'
                    value={formValue.OverallRatings}
                    size="large"
                    sx={window.mobileAndTabletCheck()?{
                        width: "80%",
                        justifyContent: "space-between",
                        fontSize:"2.5rem"
                      }:{}}
                    onChange={(event, newValue) => {
                        const updatedValue = {}
                        updatedValue.OverallRatings = newValue
                        setFormValue({
                            ...formValue,
                            ...updatedValue
                        });
                    }}
                    />
                    <div>
                        <textarea 
                        name="Comments"
                        className="comments"
                        placeholder="Describe your experience with us"
                        onChange={(event) => {
                            const updatedValue = {}
                            updatedValue.Comments = event.target.value
                            setFormValue({
                                ...formValue,
                                ...updatedValue
                            });
                        }}
                        />
                    </div>
                    <center><button className='ReviewSubmitBtn' type="submit" disabled={submitting}>Submit</button></center>

                    {/* <pre>{JSON.stringify(values, 0, 2)}</pre> */}
                </form>
                )}
            </Form>
        </Styles>
        
    );
}